@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');

.wrapper {
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    background: #181818;
    color: #fff;
    overflow: hidden;
}

.wrapper h1, h2, h3, h4 ,h5 ,h6 {
    font-weight: 300;
}

.wrapper{
    display: flex;
    flex-direction: row;
}

.left {
    display: flex;
    flex-direction: column;
    flex: 1;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.right{
    flex: 1;
}


.signBtn{
    width: 100%;
}

.secondaryBtn .or,
.secondaryBtn .links{
    width: 60%;
}

.links a{
    display: block;
    color: #fff;
    text-decoration: none;
    margin-bottom: 1rem;
    text-align: center;
    font-size: 0.9rem;
}

/* Sign In*/

.signIn{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
    padding-bottom: 1rem;
}

.signForm{
    width: 80%;
    padding-bottom: 3rem;
}

.signLogo{
    margin-bottom: 8vh;
}

.signLogoImg{
    width: 300px;
}

.signLabel{
    font-size: 0.9rem;
    line-height: 2rem;
    font-weight: 500;
}

.signInput{
    margin-bottom: 1.3rem;
    width: 100%;
    border-radius: 2px;
    background: #181818;
    border: 1px solid #555;
    color: #ccc;
    padding: 0.5rem 1rem;
    line-height: 1.3rem;
}

.signIn .or{
    display: flex;
    flex-direction: row;
    margin-bottom: 1.2rem;
    align-items: center;
}

.signIn .or .bar {
    flex: auto;
    border: none;
    height: 1px;
    background: #aaa;
}

.signIn .or span{
    color: #ccc;
    padding: 0 0.8rem;
}

/* Showcase */
.showcase{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100vh;
    background: url('https://image.ibb.co/cO9Lxq/login-bg.jpg') no-repeat center center / cover;

}

.showcaseText{
    font-size: 3rem;
    width: 100%;
    color: #fff;
    margin-bottom: 1.5rem;
}

.showcaseContent .secondaryBtn{
    width: 60%;
    margin: auto;
}

/* Footer */
.mainFooter{
    color: #ccc;
    text-align: center;
    font-size: 0.8rem;
    max-width: 80%;
    padding-top: 5rem;
}

.mainFooter a{
    color: #f96816;
    text-decoration: underline;
}

/* Button */
.primaryBtn{
    padding: 0.7rem 1rem;
    height: 2.7rem;
    display: block;
    border: 0;
    border-right: 2px;
    font-weight: 500;
    background: #f96816;
    color: #fff;
    text-decoration: none;
    cursor: pointer;
    text-align: center;
    transition: background-color 0.5s
}

.primaryBtn:hover{
    background-color: #ff7b39;
}

.secondaryBtn{
    padding: 0.7rem 1rem;
    height: 2.7rem;
    display: block;
    border: 1px solid #f4f4f4;
    border-radius: 2px;
    font-weight: 500;
    background: none;
    color: #fff;
    text-decoration: none;
    cursor: pointer;
    text-align: center;
    transition: all 0.5s;
}

.secondaryBtn:hover{
    border-color: #ff7b39;
    color: #ff7b39;
}
/* Medie Queries */
@media (min-width: 1200px) {
    .left{
        flex:4
    }
    .right{
        flex:6
    }
}

@media(max-width: 768px) {
    body{
        overflow: auto;
    }

    .right{
        display: none
    }

    .left{
        justify-content: start;
        margin-top: 4vh;
    }

    .signIn .signLogo{
        margin-bottom: 2vh;
    }

    .signIn .signInput {
        margin-bottom: 0.7rem;
    }

    .mainFooter{
        padding-top: 1rem;
    }
}


